import React from "react";

const Error404 = () => {

	return (
		<div className="container">
			<div className="card">
				<div className="card-body text-center">
					<h1>404 Error</h1>
					<p className="lead">Page not found :(</p>
				</div>
			</div>
		</div>
	);

}

export default Error404;



